// A-1: BaseAxios: aseAxiosÎäî axios instanceÏôÄ ÏÑ§Ï†ï, Interceptor Îì§ÏùÑ Ï†ïÏùò ÏÉòÌîå
import axios from 'axios';
// import { Token } from 'utils.ts';

// Default Ìó§Îçî Ï†ïÎ≥¥ Í∏∞Ïû¨
// content-typeÏùÄ json Ïù¥Íµ¨
// Î™®Îì†Í≤ΩÏö∞ ÌóàÏö©
const DEFAULT_HEADERS = {
    'Content-Type': 'application/json',
    'Access-Controll-Allow-Origin': '*'
}

// const AUTH_HEADERS = {
//     'Content-Type': 'application/json',
//     'Access-Controll-Allow-Origin': '*',
//     'Authorization': Token.getToken()
// }

// const FORM_HEADERS = {
//     'Content-Type': 'multipart/form-data',
//     'Accept': 'application/json',
//     'Access-Controll-Allow-Origin': '*',
//     'Authorization': Token.getToken()
// }

// Îã§ÏÇ¨Ïö© base url ÏÑ§Ï†ï
const BASE_URL = 'http://api.stickinteractive.com'

// ÏùëÎãµÍ∞ÄÎ°úÏ±ÑÏñ¥ Ïû¨Í∞ÄÍ≥µ(axios Ïù∏Ïä§ÌÑ¥Ïä§Î•º Îß§Í∞úÎ°ú Ï†ÑÎã¨ Î∞õÏùå)
function responseInterceptors(axiosInst) {
    axiosInst.interceptors.response.use(
        function (response) {
            console.log("---response:", response)
            // Îã§Î•∏ Ï†ïÎ≥¥Î•º Î¨¥ÏãúÌïòÍ≥† data Î≥∏Î¨∏ÎßåÏùÑ Î¶¨ÌÑ¥
            return response.data;
        },
        function (error) {
            if (error.response.status === 403) {
                // Ïù∏Ï¶ù ÎßåÎ£å
                return Promise.reject({
                    code: 403,
                    message: 'Ïù∏Ï¶ù Í∏∞Í∞ÑÏù¥ ÎßåÎ£å ÎêòÏóàÏäµÎãàÎã§.',
                    
                });
             } else if (error.response.status === 401) {
                 // Ïù∏Ï¶ù Ï†ïÎ≥¥ Î∂àÏùºÏπò
                return Promise.reject({
                    code: 401,
                    message: 'Ïù∏Ï¶ù Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§.'
                });
            }
        }
    )

    // Í∞ÄÎ°úÏ±ÑÏñ¥ ÏàòÏ†ïÎêú promise Ï†ïÎ≥¥Î•º Î≥¥ÎÉÑ
    return axiosInst;
}

// Ïù∏Ï¶ùÏù¥ ÌïÑÏöî ÏóÜÎäî Í∏∞Î≥∏ ÌÜµÏã† axios instance
export function axiosDefault() {
    // axios Ïù∏Ïä§ÌÑ¥Ïä§ Ï†ÑÎã¨
    // responseInterceptors ÏóêÏÑú response.dataÎ•º Î∞òÌôò Î∞õÍ±∞ÎÇò, error PromiseÎ•º Î∞òÌôòÎ∞õÏùå
    return responseInterceptors(
        axios.create({
            baseURL: BASE_URL,
            headers: DEFAULT_HEADERS,
        })
    )
}

